
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: 'home',
data() {
return {
activeTab: this.$route.query.tabId ? parseInt(this.$route.query.tabId) : 0,
menuList:[
{
id:1,
name:'今天',
icon:'icon-date',
url:'/main/day'
},
{
id:2,
name:'统计分析',
icon:'icon-stats',
url:'/main/stats'
},
{
id:3,
name:'系统管理',
icon:'icon-guanli',
url:'/main/system'
},
{
id:4,
name:'我的',
icon:'icon-my',
url:'/main/my'
}
]
}
},
components:{
Icon,
Tabbar,
TabbarItem
},
methods: {
handleTabChange(val) {
this.activeTab = val - 1;
for (const item of this.menuList) {
if(item.id == val){
this.$router.push({
path:item.url,
query:{
tabId:val - 1
}
})
break;
}
}
}
}
}



